.App{
  padding : 5%;
  text-align : center;
  font-size : 16px;
}

.party-popper {
  width: 200px;
  height: 200px;
}

.fade{
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.age-stats{
  opacity: 100;
}

@-webkit-keyframes fade-in {
  from{ opacity: 0 }
  to{ opacity: 100 }

}

@keyframes fade-in {
  from{ opacity: 0 }
  to{ opacity: 100 }

}



